/* eslint-disable max-len */
/* eslint-disable no-unused-expressions */
/* eslint-disable no-plusplus */
/* eslint-disable no-param-reassign */
/* eslint-disable no-restricted-properties */
/* eslint-disable class-methods-use-this */
import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import CreateElements from './CreateElements';

export default class App {
  constructor() {
    this.state = {
      // API_KEY: 'AIzaSyBieBEtePDgoxNM7RGrswogEG3z__QgcDo&type',
      API_KEY: 'AIzaSyBbWnY9aBf7_U99aeuln0_u6DjYqE3Z0BI&type',
      name: '',
      nextPageToken: '',
      itemsCounter: 0,
      indexToNewRequest: [],
      i: 0,
      clientX: null,
      locked: false,
      windowWidth: 0,
      ini: 0,
      fin: 0,
      transition: null,
      animationTime: 0,
      n: 0,
      itemsOnPage: 4,
    };
  }

  start() {
    const root = CreateElements.createRoot();
    const input = CreateElements.createInput();
    const slider = CreateElements.createSlider();
    const logo = CreateElements.createLogo();
    const controlButtons = CreateElements.createControlButtons();
    this.size();
    this.addEventListeners(input, slider, controlButtons);
    root.appendChild(input);
    root.appendChild(slider);
    root.appendChild(controlButtons);
    document.body.appendChild(logo);
    this.state.model = new AppModel(this.state);
    this.state.view = new AppView();
    this.state.slider = slider;
  }

  // async renderItems() {
  //   const logo = document.querySelector('.logo');
  //   if (this.state.itemsCounter === 0) logo.classList.add('animate');
  //   const model = new AppModel(this.state);
  //   const data = await model.getAllInfo();
  //   this.state.nextPageToken = data.nextPageToken;
  //   this.state.itemsCounter += data.itemsInfo.length;
  //   document.querySelector('.slider').style.setProperty('--n', this.state.itemsCounter);
  //   const view = new AppView(data.itemsInfo);
  //   requestAnimationFrame(() => view.render());
  //   this.showButtons();
  //   document.querySelector('.logo').classList.remove('animate');
  // }
  async renderItems() {
    const logo = document.querySelector('.logo');
    if (this.state.itemsCounter === 0) logo.classList.add('animate');
    const data = await this.state.model.getAllInfo();
    this.state.nextPageToken = data.nextPageToken;
    this.state.itemsCounter += data.itemsInfo.length;
    this.state.slider.style.setProperty('--n', this.state.itemsCounter);
    requestAnimationFrame(() => this.state.view.render(data.itemsInfo));
    requestAnimationFrame(() => this.showButtons());
    document.querySelector('.logo').classList.remove('animate');
  }

  getIndexToNextRequest() {
    const nextRequest = this.state.itemsCounter - this.state.itemsOnPage * 2;
    let counter = 0;
    let index = 0;
    while (counter < nextRequest) {
      counter += this.state.itemsOnPage;
      index += 1;
    }
    if (this.state.i === index) {
      this.state.indexToNewRequest.push(index);
      return true;
    }
    return false;
  }

  async reRender() {
    while (this.state.slider.firstChild) {
      this.state.slider.removeChild(this.state.slider.firstChild);
    }
    this.state.indexToNewRequest = [];
    this.state.itemsCounter = 0;
    this.state.nextPageToken = '';
    this.state.i = 0;
    this.renderItems();
    this.setButtonsValue();
    this.state.slider.style.setProperty('--i', 0);
  }

  size() {
    this.state.windowWidth = window.innerWidth;
    if (window.innerWidth < 1360 && window.innerWidth > 1100) this.state.itemsOnPage = 3;
    if (window.innerWidth < 1100 && window.innerWidth > 800) this.state.itemsOnPage = 2;
    if (window.innerWidth < 800 && window.innerWidth > 600) this.state.itemsOnPage = 1;
  }

  unify(e) {
    return e.changedTouches ? e.changedTouches[0] : e;
  }

  lock(e) {
    this.state.slider.style.cursor = 'grabbing';
    this.state.clientX = this.unify(e).clientX;
    this.state.locked = true;
  }

  move(e) {
    const NF = 40;
    if (this.state.locked) {
      const dx = this.unify(e).clientX - this.state.clientX;
      const s = Math.sign(dx);
      let f = +(s * dx / this.state.windowWidth).toFixed(2);
      this.state.ini = this.state.i - s * f;
      if ((this.state.i > 0 || s < 0)
      && (this.state.i < (this.state.itemsCounter - 1 / this.state.itemsOnPage) || s > 0)
      && f > 0.2) {
        this.state.i -= s;
        f = 1 - f;
      }
      this.state.fin = this.state.i;
      this.state.animationTime = Math.round(f * NF);
      this.state.n = 2 + Math.round(f);
      this.ani();
      this.state.clientX = null;
      this.state.locked = false;
      this.setButtonsValue();
      this.state.slider.style.cursor = 'grab';
    }
  }

  stopAni() {
    cancelAnimationFrame(this.state.transition);
    this.state.transition = null;
  }

  ani(start = 0) {
    const bounceOut = (k, a = 2.75, b = 1.5) => 1 - Math.pow(1 - k, a)
      * Math.abs(Math.cos(Math.pow(k, b) * (this.state.n + 0.5) * Math.PI));
    if (start === this.state.animationTime) {
      this.stopAni();
      return;
    }
    this.state.slider.style.setProperty('--i', this.state.ini + (this.state.fin - this.state.ini) * bounceOut(start / this.state.animationTime));
    this.state.transition = requestAnimationFrame(this.ani.bind(this, ++start));
  }

  drag(e) {
    e.preventDefault();
    if (this.state.locked) {
      const dx = this.unify(e).clientX - this.state.clientX;
      const f = +(dx / this.state.windowWidth).toFixed(2);
      requestAnimationFrame(() => {
        this.state.slider.style.setProperty('--i', this.state.i - f);
      });
    }
  }

  ButtonsHandler(e) {
    if (e.target.classList.contains('current')) return;
    this.state.slider.style.transition = 'transform .3s ease-in-out';
    requestAnimationFrame(() => {
      this.state.i = +e.target.value;
      this.state.slider.style.setProperty('--i', this.state.i);
      this.setButtonsValue();
    });
  }

  showTooltip(e) {
    if (e.target.classList.contains('current')) return;
    if (document.querySelector('.next').firstChild.innerHTML === '') this.setButtonsValue();
    e.target.firstChild.classList.remove('hidden');
  }

  hideTooltip(e) {
    if (e.target.classList.contains('current')) return;
    e.target.firstChild.classList.add('hidden');
  }

  setButtonsValue() {
    const beforePrev = document.querySelector('.bfPrev');
    const prev = document.querySelector('.prev');
    const current = document.querySelector('.current');
    const next = document.querySelector('.next');
    const value = this.state.i;
    this.state.i >= 1 ? prev.classList.remove('hidden') : prev.classList.add('hidden');
    this.state.i >= 2 ? beforePrev.classList.remove('hidden') : beforePrev.classList.add('hidden');
    current.value = value;
    current.firstChild.innerHTML = value + 1;
    current.innerHTML = +value + 1;
    next.value = value + 1;
    next.firstChild.innerHTML = value + 2;
    prev.value = value - 1;
    prev.firstChild.innerHTML = value;
    beforePrev.value = value - 2;
    beforePrev.firstChild.innerHTML = value - 1;
    if (this.getIndexToNextRequest()) this.renderItems();
  }

  showButtons() {
    if (this.state.itemsCounter > 0) document.querySelector('.current').classList.remove('hidden');
    if (this.state.itemsCounter > this.state.itemsOnPage) document.querySelector('.next').classList.remove('hidden');
  }

  addEventListeners(input, slider, controlButtons) {
    input.addEventListener('change', (e) => {
      this.state.name = e.target.value;
      this.state.itemsCounter ? this.reRender() : this.renderItems();
    });
    input.addEventListener('focusin', (e) => {
      e.target.value = '';
    });
    window.addEventListener('resize', e => this.size(e), false);
    slider.addEventListener('mousedown', e => this.lock(e), false);
    slider.addEventListener('touchstart', e => this.lock(e), false);
    slider.addEventListener('mousemove', e => this.drag(e), false);
    slider.addEventListener('touchmove', e => this.drag(e), false);
    slider.addEventListener('mouseup', e => this.move(e), false);
    slider.addEventListener('touchend', e => this.move(e), false);
    slider.addEventListener('transitionend', () => {
      this.state.slider.style.transition = 'none';
    });
    controlButtons.childNodes.forEach((e) => {
      e.addEventListener('click', evt => this.ButtonsHandler(evt), false);
      e.addEventListener('mousedown', evt => this.showTooltip(evt), false);
      e.addEventListener('mouseup', evt => this.hideTooltip(evt), false);
    });
  }
}
